/* src/styles/App.css */

/* Global reset for margin and padding */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Body styles */
body {
  font-family: Arial, sans-serif;
  background-color: #f4f4f9;
  color: #333;
  line-height: 1.6;
  padding: 20px;
}

/* Main container */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
}

/* Navigation bar styles */
nav {
  width: 100%;
  background-color: #333;
  color: #fff;
  padding: 10px 0;
  position: sticky;
  top: 0;
  z-index: 10;
}

nav ul {
  list-style: none;
  display: flex;
  justify-content: center;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  text-decoration: none;
  color: #fff;
  font-size: 1.2rem;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #5cb85c; /* Hover effect for links */
}

/* Content container for each page */
.content-container {
  width: 80%;
  max-width: 1200px;
  margin-top: 30px;
}

/* Footer styles (if you add one) */
footer {
  margin-top: auto;
  padding: 10px 0;
  background-color: #333;
  color: white;
  text-align: center;
}
